import { useHistory } from "react-router-dom";
import { useContext, useEffect } from "react";
import UserContext from "../UserContext";
function EnterDealDetails() {
  const history = useHistory();
  const globalState = useContext(UserContext);
  const goBack = () => history.push("/");
  const dealDetails = globalState.temporaryStorage;
  const setDealDetails = globalState.setTemporaryStorage;
  useEffect(() => {
    globalState.setTemporaryStorage({
      price: "",
      numberFromCard: "",
      moreDetails: "",
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const formOnSubmit = (Event) => {
    Event.preventDefault();
    history.push("/credit_page");
  };
  return (
    <div>
      <div className="edit_business">
        <div>
          <button
            onClick={goBack}
            className="go_back h-20 w-40 m-4 flex justify-center items-center border border-transparent text-sm text-xl font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
          >
            חזור אחורה{" "}
          </button>
          {!dealDetails ? null : (
            <div className="min-h-screen flex justify-center bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
              <div className="max-w-md w-full space-y-8">
                <div>
                  <img
                    className="mx-auto h-12 w-auto"
                    src="https://tailwindui.com/img/logos/workflow-mark-indigo-600.svg"
                    alt="Workflow"
                  />
                  <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
                    פרטי עסקה
                  </h2>
                </div>
                <form
                  onSubmit={(Event) => formOnSubmit(Event)}
                  className="mt-8 space-y-6"
                >
                  {/* <input type="hidden" name="remember" value="true" /> */}
                  <div dir="rtl" className="rounded-md shadow-sm -space-y-px">
                    <div className="label_and_input">
                      <label htmlFor="price">מחיר:</label>
                      <input
                        dir="ltr"
                        id="price"
                        value={dealDetails.price}
                        type="text"
                        pattern="\d{1,3}(?:[.,]\d{2})"
                        maxLength="6"
                        required
                        className="edit_business_input appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-t-md focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm "
                        placeholder="דוגמה: 12.50"
                        onChange={(e) => {
                          dealDetails.price = e.target.value;
                          setDealDetails({ ...dealDetails });
                        }}
                      />
                    </div>
                    <div className="label_and_input">
                      <label htmlFor="firstname">תאור:</label>
                      <input
                        value={dealDetails.moreDetails}
                        id="moreDetails"
                        type="text"
                        maxLength="50"
                        className="edit_business_input appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-t-md focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm "
                        placeholder="תאור המוצר (מומלץ)"
                        onChange={(e) => {
                          dealDetails.moreDetails = e.target.value;
                          setDealDetails({ ...dealDetails });
                        }}
                      />
                    </div>
                  </div>

                  <div>
                    <button
                      type="submit"
                      className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                    >
                      <span className="absolute left-0 inset-y-0 flex items-center pl-3">
                        <svg
                          className="h-5 w-5 text-indigo-500 group-hover:text-indigo-400"
                          xmlns="http://www.w3.org/2000/svg"
                          viewBox="0 0 20 20"
                          fill="currentColor"
                          aria-hidden="true"
                        >
                          <path
                            fillRule="evenodd"
                            d="M5 9V7a5 5 0 0110 0v2a2 2 0 012 2v5a2 2 0 01-2 2H5a2 2 0 01-2-2v-5a2 2 0 012-2zm8-2v2H7V7a3 3 0 016 0z"
                            clipRule="evenodd"
                          />
                        </svg>
                      </span>
                      המשך
                    </button>
                  </div>
                </form>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
export default EnterDealDetails;
